import React , { Component} from 'react';


class Tabs  extends Component {

    constructor(props){
        super(props);
        this.state= {active: 1};
    }

    handlerTabClick = e=>{//+e.target.atributes["data-name"];
      let active = +e.target.attributes['data-name'].value;
      this.setState({...this.state , active})
    };

    render(){


        return (
            <div className={`tabs my-5`}>
                <div className="listName my-5">
                    <ul className="list-unstyled d-flex justify-content-around ml-3">
                        {this.props.people.map( (person , index) => {
                            return <li id={`personId-${person["name"]}`} key={person.id}>{person["name"]}</li>
                        })}
                    </ul>

                </div>
                <div className="tabs">
                    {this.props.tabs.map( ({id  , name}) => {
                        return   (<button
                            key={id}
                            data-name={id}
                            onClick={this.handlerTabClick}
                            className ={` ${this.state.active === id && 'active'} mx-2  btn btn-outline-success`}>{name}
                        </button>)
                             }
                      )
                    }
                </div>


                <div className="tabsContent">
                    {
                       this.props.tabs.map(({id , content})=>{

                           return (
                           this.state.active === id &&
                               <div
                                   key={id}
                                   data-active={id}  >
                                   {content}
                               </div>

                           )
                       })


                    }
                </div>

            </div>
        )

    }

}


export default Tabs;