import React from 'react';
//import  PropTypes  from "prop-types";
function  Counter (props) {


let handlerClick = (e , text="sometext in argument" , counter)=>{
   props.linkClick(e , text , props.counter )
};
//const {counter , counterClick} = data;
            return (
        <div className="Counter">
            {props.counter < 5 ? <div>counter less than 5 </div> : <div>counter more than  5</div>}
     Counter! Count is : {props.counter}
            <button onClick={props.counterClick} className="btn btn-success">Click to Count</button>

            <a onClick={handlerClick} href={props.linkHref}>{props.linkContent}</a>
        </div>)

}

export default Counter;